// app/admin/settings/page.tsx
import { redirect } from "next/navigation";
import { auth } from "@clerk/nextjs/server";
import dbConnect from "@/lib/mongodb";
import { BankSetting, Profile } from "@/lib/models";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { SettingsForm } from "@/components/admin/settings-form";

// Default settings to create if none exist
const DEFAULT_SETTINGS = [
  {
    settingKey: "home_loan_rate",
    settingValue: "8.5",
    description: "Annual interest rate for home loans (%)",
  },
  {
    settingKey: "personal_loan_rate",
    settingValue: "12.0",
    description: "Annual interest rate for personal loans (%)",
  },
  {
    settingKey: "business_loan_rate",
    settingValue: "10.5",
    description: "Annual interest rate for business loans (%)",
  },
  {
    settingKey: "education_loan_rate",
    settingValue: "9.0",
    description: "Annual interest rate for education loans (%)",
  },
  {
    settingKey: "auto_loan_rate",
    settingValue: "9.5",
    description: "Annual interest rate for auto loans (%)",
  },
];

export default async function AdminSettingsPage() {
  const { userId } = await auth();

  if (!userId) {
    redirect("/sign-in");
  }

  await dbConnect();

  // Check if user is admin
  const profile = await Profile.findOne({
    $or: [{ userId: userId }, { clerkId: userId }],
  }).lean();

  if (!profile?.isAdmin) {
    redirect("/dashboard");
  }

  // Fetch all settings
  let settings = await BankSetting.find({}).lean();

  // If no settings exist, create defaults
  if (!settings || settings.length === 0) {
    console.log("⚠️ No settings found, creating defaults...");
    try {
      const createdSettings = await BankSetting.insertMany(
        DEFAULT_SETTINGS.map((s) => ({
          ...s,
          updatedBy: userId,
        }))
      );
      settings = createdSettings;
      console.log("✅ Default settings created");
    } catch (error) {
      console.error("Error creating default settings:", error);
      settings = [];
    }
  }

  // Ensure all settings have required properties
  const validSettings = (settings || []).filter(
    (s) => s && s.settingKey && typeof s.settingKey === "string"
  );

  // Separate interest rate settings from other settings
  const interestSettings = validSettings.filter(
    (s) => s.settingKey && s.settingKey.endsWith("_loan_rate")
  );

  const otherSettings = validSettings.filter(
    (s) => s.settingKey && !s.settingKey.endsWith("_loan_rate")
  );

  // Serialize for client component
  const serializedInterestSettings = interestSettings.map((s) => ({
    id: s._id?.toString() || "",
    settingKey: s.settingKey || "",
    settingValue: s.settingValue || "",
    description: s.description || "",
  }));

  const serializedOtherSettings = otherSettings.map((s) => ({
    id: s._id?.toString() || "",
    settingKey: s.settingKey || "",
    settingValue: s.settingValue || "",
    description: s.description || "",
  }));

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold">System Settings</h1>
        <p className="text-muted-foreground">
          Configure bank-wide settings and interest rates
        </p>
      </div>

      <div className="grid gap-6">
        <Card>
          <CardHeader>
            <CardTitle>Loan Interest Rates</CardTitle>
          </CardHeader>
          <CardContent>
            {serializedInterestSettings.length === 0 ? (
              <p className="text-sm text-muted-foreground">
                No interest rate settings found.
              </p>
            ) : (
              <SettingsForm
                settings={serializedInterestSettings}
                type="interest"
              />
            )}
          </CardContent>
        </Card>

        {serializedOtherSettings.length > 0 && (
          <Card>
            <CardHeader>
              <CardTitle>Other Settings</CardTitle>
            </CardHeader>
            <CardContent>
              <SettingsForm settings={serializedOtherSettings} type="other" />
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  );
}
